html,body{
	margin:0;
	width: 100%;
	height: 100%;
	padding: 0;
}

.box{
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: column;
}

.quoteBox{
	background-color: white;
	width: 80%;
	max-width: 500px;
	height: auto;
	min-height: 200px;
	margin-bottom: 100px;
	position: relative;
	border-radius: 5px;

}

.quoteBox .quote{
	margin: 20px;
	font-size: 25px;
	text-align: center;
}

.quoteBox .quote span{
	font-size: 60px;


}
.quoteBox .character{
	width: 90%;
	text-align: right;
}

.quoteBox .button button{
	float: right;
	margin:40px;
	width: 100px;
	height: 30px;
	color: white;
	border:none;
	font-size: 14px;
	cursor: pointer;
}

.quoteBox .twitter a{
	float: left;
	margin:40px;
	text-decoration: none;
	font-size: 30px;
}

.footer{
	text-align: center;
	font-family: monospace;
	color: maroon;
	font-size: 18px;
}

@media screen and (max-width:800px){
	.quoteBox .quote{
		font-size: 18px;
	}
}







